import React from "react";

function OffersCard({ heading, url, src }) {
  return (
    <div className="offersCardContainer">
      <a href={url}>
        <img className="offersCardImage" src={src} />
      </a>
      <div class="offersCardText">
        <h1 className="offersCardHeading">{heading}</h1>
        <a className="offersCardLink" href={url}>
          Shop now
        </a>
      </div>
    </div>
  );
}

export default OffersCard;
