import React from "react";
import Carousel from "./components/Carousel";
import carouselData from "./carouselData";
import CardSmall from "./components/CardSmall";
import ProductCard from "./components/ProductCard";
import productsHomeData from "./productsHomeData";
import OffersCard from "./components/OffersCard";
import CardMedium from "./components/CardMedium";

function Main() {
  const productsHomeAll = productsHomeData.map((productData) => (
    <ProductCard key={productData.name} productData={productData} />
  ));
  return (
    <div className="mainContentWrapper">
      <Carousel carouselData={carouselData} />

      <h1 className="headingMain">Shop by category</h1>

      <div className="cardSmallRow">
        <CardSmall
          text="Fruit & veg"
          src="./card-small/fruit-and-veg.png"
          url="#"
        />
        <CardSmall
          text="Meat & Fish"
          src="./card-small/meat-and-fish.png"
          url="#"
        />
        <CardSmall
          text="Food cupboard"
          src="./card-small/food-cupboard.jpg"
          url="#"
        />
        <CardSmall text="Household" src="./card-small/household.png" url="#" />
      </div>

      <h1 className="headingMain">Watch the sport</h1>
      <div className="productDisplayHome">{productsHomeAll}</div>
      <div className="offersCardRow">
        <OffersCard
          heading="Summer sipping"
          src="./card-offers/summer-sipping.jpg"
          url="#"
        />
        <OffersCard
          heading="Picnic offers"
          src="./card-offers/picnic-offers.jpg"
          url="#"
        />
      </div>
      <div className="offersCardRow">
        <OffersCard
          heading="Fire up the BBQ"
          src="./card-offers/fire-up-the-bbq.jpg"
          url="#"
        />
        <OffersCard
          heading="Beauty offers"
          src="./card-offers/beauty-offers.jpg"
          url="#"
        />
      </div>

      <h1 className="headingMain">Shopping with us</h1>
      <div className="cardSmallRow">
        <CardSmall
          text="Nectar points"
          src="./card-small/nectar-points.jpg"
          url="#"
        />
        <CardSmall
          text="Click & collect"
          src="./card-small/click-and-collect.jpg"
          url="#"
        />
        <CardSmall
          text="Shop offers"
          src="./card-small/shop-offers.jpg"
          url="#"
        />
        <CardSmall
          text="Groceries app"
          src="./card-small/groceries-app.png"
          url="#"
        />
      </div>

      <div className="cardMediumRow">
        <CardMedium
          heading="Better for the planet"
          src="./card-medium/better-for-the-planet.jpg"
          url="#"
          bgDark={true}
        />
        <CardMedium
          heading="Help everyone eat better"
          src="./card-medium/help-everyone-eat-better.jpg"
          url="#"
          bgDark={false}
        />
      </div>
    </div>
  );
}

export default Main;
