import React from "react";

function CardMedium({ heading, src, url, bgDark }) {
  return (
    <div className="cardMediumContainer">
      <a href={url}>
        <img className="cardMediumImage" src={src} alt={heading} />
      </a>
      <div className="cardMediumContent">
        <h1
          className={
            "cardMediumHeading" + " " + (bgDark && "cardMediumHeadingLight")
          }
        >
          {heading}
        </h1>
        <button
          className={
            "cardMediumButton" + " " + (bgDark && "cardMediumButtonLight")
          }
        >
          Find out more
        </button>
      </div>
    </div>
  );
}

export default CardMedium;
