import React, { useState } from "react";
import Header from "./Header";
import Main from "./Main";
import Footer from "./Footer";
import Sidebar from "./Sidebar";

import "./index.css";
import TrolleyContext from "./TrolleyContext";

function App() {
  const [trolleyCurrent, setTrolleyCurrent] = useState([]);

  return (
    <div>
      <TrolleyContext.Provider value={{ trolleyCurrent, setTrolleyCurrent }}>
        <Header />
        <div className="bodyWrapper">
          <Main />
          <Sidebar />
        </div>
      </TrolleyContext.Provider>
      <Footer />
    </div>
  );
}

export default App;
