import React, { useRef, useState } from "react";
import { useHistory, Link } from "react-router-dom";
import { useAuth } from "./AuthContext";

function LogInPage() {
  const emailRef = useRef();
  const passwordRef = useRef();
  const passwordConfirmationRef = useRef();
  const [errorMessage, setErrorMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const { login } = useAuth();

  async function handleSubmit(e) {
    e.preventDefault();

    try {
      setErrorMessage("");
      setLoading(true);
      await login(emailRef.current.value, passwordRef.current.value);
      history.push("/");
    } catch {
      setErrorMessage("Log in failed");
    }
    setLoading(false);
  }

  return (
    <div class="userFormContainer">
      <h1>Log in</h1>
      <form className="userForm" onSubmit={handleSubmit}>
        <label>
          Email address
          <input type="email" ref={emailRef} />
        </label>
        <label>
          Password
          <input type="password" ref={passwordRef} />
        </label>
        <p class="userFormError">{errorMessage ? errorMessage : null}</p>
        <button
          className="userFormSubmitButton"
          disabled={loading ? true : false}
        >
          Log in
        </button>
      </form>
      <p className="userFormRegisterText">New to Sainsbury's Groceries?</p>
      <Link className="userFormRegisterLink" to="/signup">
        <p className="userFormRegister">Register here</p>
      </Link>
    </div>
  );
}

export default LogInPage;
