import React, { useRef, useState } from "react";
import { useAuth } from "./AuthContext";
import { useHistory } from "react-router-dom";

function SignUpPage() {
  const emailRef = useRef();
  const passwordRef = useRef();
  const passwordConfirmationRef = useRef();
  const { signup } = useAuth();
  const [errorMessage, setErrorMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const history = useHistory();

  async function handleSubmit(e) {
    e.preventDefault();

    if (passwordRef.current.value.length < 6) {
      return setErrorMessage("Please set a longer password");
    }
    if (passwordRef.current.value !== passwordConfirmationRef.current.value) {
      return setErrorMessage("Passwords do not match");
    }

    try {
      setErrorMessage("");
      setLoading(true);
      await signup(
        emailRef.current.value,
        passwordRef.current.value,
        passwordConfirmationRef.current.value
      );
      history.push("/");
    } catch {
      setErrorMessage("Unknown Error: Failed to create account");
    }
    setLoading(false);
  }

  return (
    <div class="userFormContainer">
      <h1>Sign Up Page</h1>
      <form className="userForm" onSubmit={handleSubmit}>
        <label>
          Email
          <input type="email" ref={emailRef} />
        </label>
        <label>
          Password
          <input type="password" ref={passwordRef} />
        </label>
        <label>
          Re-enter password
          <input type="password" ref={passwordConfirmationRef} />
        </label>
        <p class="userFormError">{errorMessage ? errorMessage : null}</p>
        <button
          className="userFormSubmitButton"
          disabled={loading ? true : false}
        >
          Sign Up
        </button>
      </form>
    </div>
  );
}

export default SignUpPage;
